    <code-highlight class='reReW' language="javascript">
    <pre>
// Function that lets users embed up to 5 pictures in a post

fileUpload: function(event){
    const input = event.target;
    if (input.files && input.files.length &lt;= 5 && this.mediaList.length &lt;= 5 && this.mediaList.length + input.files.length &lt;= 5) {
        input.files.forEach(pic => {
            const reader = new FileReader();
            reader.onload = (e) => {
                this.mediaList.push({
                    picId: this.mediaId + 1,
                    desc: '',
                    source: e.target.result,
                    editable: false
                });
                const lastMedia = this.mediaList.length - 1;
                this.mediaId = this.mediaList[lastMedia].picId;        
            }
            reader.readAsDataURL(pic);
        });
    } else {
        this.writeCallBackMessage = 'You can only add up to a maximum of 5 images';
    }
},</pre>
    </code-highlight>
    <code-highlight class='reReW' language="javascript">
    <pre>
// Function that checks credentials and logs users in

login: function(){
    if(this.loginInProgress == false){
        this.loginInProgress = true;
        this.axios
            .post('php/login.php', {
                email_l: this.loginEmail,
                pass_l: this.loginPass,
                remember: this.rememberMe
            }).then(response => {
                if(response.data.response_type == 'bad'){
                    this.loginCallback = response.data.res
                } else if(response.data.response_type == 'good'){
                    this.loginCallback = response.data.res
                    this.showLogInModalIs = false
                    this.$store.commit('loggedStateChange', 
                    {isLogged: true, isUserImage: response.data.res.userImage, userId: response.data.res.currentUser});
                }
            })
        this.loginInProgress = false;
    }
},</pre>
