<template>
    <div id="builderWrap">
        <div class="sectionTitleWrap">Web Builder</div>
        <div class="sectionDescription">
            A web builder similar to Webflow where users can drag elements on a canvas and add styling to them in a visual way while preserving all the aspects that similar tools lack, like semantic markup and accessibility.
            Allowing users to build web pages that take advantage of grid and flex layouts and convert design into web redy responsive html.
        </div>
        <div class="DIVIDER"></div>
        <div style="align-items: center; margin-top: 40px" class="section">
            <DescBox boxImg="./info/tools.svg" boxTitle="Building tools" boxText="Web building tools with draggable html elements easelly editable"/>
            <div class="toolsWr">
                <div style='border-right: 5px solid #202020;' class="imgWrapTol">
                    <img src="../../public/tools/mouse.svg" class="toolsImg">
                </div>
                <div class="imgWrapTol">
                    <img src="../../public/tools/book.svg" class="toolsImg">
                </div>
                <div class="imgWrapTol">
                    <img src="../../public/tools/box.svg" class="toolsImg">
                </div>
                <div class="imgWrapTol">
                    <img src="../../public/tools/list.svg" class="toolsImg">
                </div>
                <div class="imgWrapTol">
                    <img src="../../public/tools/text.svg" class="toolsImg">
                </div>
                <div class="imgWrapTol">
                    <img src="../../public/tools/cog.svg" class="toolsImg">
                </div>                                                                   

            </div>
        </div>
        <div class="DIVIDER"></div>
        <div class="section canSec">
            <DescBox boxImg="./tools/rule.svg" boxTitle="Canvas" boxText="Add and edit html elements using an intuitive visual interface that updates in real time"/>
            <div class="canvas">
                <div class="canvaEl">
                    <div class="canvaEl"></div>
                </div>
                    <div style="margin-top: 10px" class="canvaEl"></div>
            </div>
        </div>
        <div class="DIVIDER"></div>
        <div id='builderCssOp' class="section">
            <DescBox boxImg="./tools/code.svg" boxTitle="Css" boxText="Quicky edit css rules for the selected element"/>
            <div class="optionWrap">
                <span>Flex</span>
                <input placeholder="flex:" type="text">
                <input placeholder="flex-grow:" type="text">
                <input placeholder="flex-shrink:" type="text">
                <input placeholder="flex-basis:" type="text">
                <input placeholder="align-items:" type="text">
                <input placeholder="justify-content:" type="text">
                <input placeholder="flex-direction:" type="text">
                <input placeholder="flex-wrap:" type="text">
                <div class="ext">
                    Show all options
                </div>
            </div>
        </div>
        <div class="DIVIDER"></div>
    <div class="section" style='align-items: center'>
            <code-highlight class='reReW' language="javascript">
    <pre>
// The virtual DOM gets passed to this function to determine
// the correct order and child-parent relation
// This function's results get passed to the DOM Renderer that 
// genertaed the HTML that apperas on the canvas so users can
// edit any element 

filter: function(val){
    var ar = {}
    for(const property in this.$store.state.bigDom) {
        if(this.$store.state.bigDom[property].parent == val){
            ar[property] = this.$store.state.bigDom[property]
        }
    }
    return ar
},</pre>
    </code-highlight>
                <code-highlight class='reReW' language="javascript">
    <pre>
// The object in $store that holds the virtual DOM
// along with any data thats associated with every HTML element
// Using objects with numbers as the Keys allows for quick access
// and less loops and checks so the canvas can be updated instantly

bigDom: {
    1: {
        parent: 'null',
        tag: 'div',
        id: '',
        classes: [],
        isWire: false,
        innerText: '',
        attr: {},
        customData: [
            {
                attrName: '',
                attrVal: '',
                uniqueCustomDataNum: 99
            }
        ]
    },
},</pre>
    </code-highlight>
            <code-highlight class='reReW' language="javascript">
    <pre>
// Function that computes all id/classes for an element 
// so users can add/remove them and everything would get 
// updated live inside the canvas

allClassComputed: function(){
    let ary = []
    for(const cls in this.$store.getters.styleList){
        if(this.$store.getters.styleList[cls].type == 'class') {
            ary.push({'clsName': '.' + cls, 'clsType': 'class'})
        } else if(this.$store.getters.styleList[cls].type == 'id'){
            ary.push({'clsName': '#' + cls, 'clsType': 'id'})
        }
    }
    return ary;
},</pre>
    </code-highlight>
    <code-highlight class='reReW' language="javascript">
    <pre>
// Function that checks what id/classes a given element has
// Takes the styles from $store and embeds them to the inline style of the element
// Used in a component that loops through a virtual DOM and renders it

filterStyles: function(classArr, id){
    if(id.length > 0){
        classArr.push(id)
    }
    if(classArr.length == 0){
        return
    }
    let parsedStylesString = '';
    let idString = '';
    classArr.forEach(cls => {
        for (const styles in this.computedStyleList[cls].rules) {
            if(this.computedStyleList[cls].type == 'class'){
                parsedStylesString += styles + ': ' + this.computedStyleList[cls].rules[styles] + '; '
            } else {
                idString += styles + ': ' + this.computedStyleList[cls].rules[styles] + '; '
            }
        }
    })
    parsedStylesString += idString; 
    return parsedStylesString;
},</pre>
    </code-highlight>
    <code-highlight class='reReW' language="javascript">
    <pre>
// The component that recursively generates
// HTML from the $store and renders it
// onto the canas

&lt;component :is="node.tag"
    v-for="(node, nodeKey, index) in nodesToRender" 
    :key="index"
    :data-innerid="nodeKey"
    :style="filterStyles(node.classes, node.id)"
    :class="defaultClass(node.tagType)">
        &#123;&#123;node.innerText}}
        &lt;ElementCreator :nodesToRender='filter(nodeKey)'/>
&lt;/component></pre>
    </code-highlight>
    </div>
    </div>
</template>

<script>
import CodeHighlight from "vue-code-highlight/src/CodeHighlight.vue";
import "vue-code-highlight/themes/duotone-sea.css";
