<template>
    <div class="tagWrapp flip">
        <div class="tagImgWrap">
            <img :src="tagImg" class="tagImg toColor">
        </div>
        <div class="tagTextWrap">
            <span class="tagText">{{tagText}}</span>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Tags',

    props: {
        tagImg: String,
        tagText: String
    }
}
</script>

<style>
    .tagWrapp{
        display: flex;
        flex-direction: row;
        padding: 0 10px;
        border-radius: 20px;
        background: #1C1C1C;
        align-items: center;
        margin: 25px;
        margin-bottom: 10px;
        box-shadow: 0 0 2px black;
    }

    .tagTextWrap{
        padding: 0 20px;
    }

    .tagImgWrap{
        border: 15px solid #232323;
        background: #232323;
        border-radius: 50%;
        margin-left: -35px;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 24px;
        width: 24px;
    }

    .tagImg{
        height: 24px;
        width: auto;
        object-fit: cover;
    }

    .tagText{
        color: white;
        font-size: 1.4em;
    }

    .toColor{
        animation: 5s 1 alternate ToColor;
        animation-delay: 500ms;
    }

    .flip{
        animation: 1s 1 alternate Flip;
        animation-delay: 500ms;
        animation-fill-mode: both;

    }

    @keyframes Flip{
        from{
            transform: rotateY(90deg);
        }

        to{
            transform: rotateY(0deg);
        }
    }

    @keyframes ToColor {
        from{
            filter: grayscale();
        }

        to{
            filter: none;
        }
    } 

</style>