export default {
    
    data(){
        return{
            emailPrint: 'john@adress.com',
            passPrint: "don'tLookAtMyPass",

            regNamePrint: 'JohnWasTaken',
            regEmailPrint: 'otherJohn@adress.com',
            regPassPrint1: 'anUncommonPassword',
            regPassPrint2: 'anUncommonPassword',
            

            socialTrigger: false,

            flexPrint1: 'flex-direction: column',
            flexPrint2: 'flex-wrap: wrap',
            flexPrint3: 'align-items: center'
        }
    },
    
    methods: {
        writeText(type, len, speed, cnt = 0){
            if(cnt < len){
                if(type == 'login'){
                    this.$store.commit('changeEmail', this.emailPrint[cnt])
                } else if (type == 'pass'){
                    this.$store.commit('changePass', this.passPrint[cnt])
                } else if (type == 'r1'){
                    this.$store.commit('changeNameR', this.regNamePrint[cnt])
                } else if(type == 'r2'){
                    this.$store.commit('changeEmailR', this.regEmailPrint[cnt])
                }else if(type == 'r3'){
                    this.$store.commit('changePassR1', this.regPassPrint1[cnt])
                }else if(type == 'r4'){
                    this.$store.commit('changePassR2', this.regPassPrint2[cnt])
                }
                cnt++
                setTimeout(()=>{this.writeText(type, len, speed, cnt)}, speed)
            }
        },
    }
}