import { createStore } from 'vuex'

export default createStore({
  state: {
    emailEmpty: '',
    passEmpty: '',


    regNameEmpty: '',
    regEmaiEmpty: '',
    regPassEmpty1: '',
    regPassEmpty2: '',


    flexEmpty1: '',
    flexEmpty2: '',
    flexEmpty3: '',

  },
  mutations: {
    changeEmail(state, v){
      state.emailEmpty += v
    },

    changePass(state, v){
      state.passEmpty += v
    },


    changeNameR(state, v){
      state.regNameEmpty += v 
    },

    changeEmailR(state, v){
      state.regEmaiEmpty += v 
    },
    changePassR1(state, v){
      state.regPassEmpty1 += v 
    },
    changePassR2(state, v){
      state.regPassEmpty2 += v 
    },

  },
  actions: {
  },
  modules: {
  }
})
