<template>
	<div>
		<Header />
		<Builder />
		<SocialMedia />
		<File />
		<div class="foot">
			Powered by 
			<span><a href="https://vuejs.org/">Vue</a></span>
			<span><a href="https://fontawesome.com/license">Font awesome</a></span>
		</div>
	</div>
</template>

<script>
import Header from './components/Header.vue'
import SocialMedia from './components/SocialMedia.vue'
import Builder from './components/Builder.vue'
import File from './components/File.vue'

import write from './mixins/write'

export default {

name: 'App',
	components: {
		Header,
		SocialMedia,
		Builder,
		File
	},

	mixins: [
		write
	],

	data(){
		return{
			socialTriggered: false,
			builderIsTriggered: false
		}
	},

	mounted: function(){
		document.title = 'Portfolio'

		window.addEventListener('scroll', ()=>{
			if(this.socialTriggered == false && document.querySelector('#socialWrapp').offsetTop <= window.scrollY + 100){
				this.socialTriggered = true
				this.writeText('login', this.emailPrint.length, 60)
				setTimeout(()=>{
					this.writeText('pass', this.passPrint.length, 40)
				}, 1300)		
				setTimeout(()=>{
					this.writeText('r1', this.regNamePrint.length, 60)
				}, 2000)
								setTimeout(()=>{
					document.querySelector('#wrongPass').style.visibility = 'visible'				
				}, 2100)
				setTimeout(()=>{
					this.writeText('r2', this.regEmailPrint.length, 80)
				}, 3000)				
				setTimeout(()=>{
					this.writeText('r3', this.regPassPrint1.length, 100)				
				}, 4000)				
				setTimeout(()=>{
					this.writeText('r4', this.regPassPrint2.length,100)					
				}, 4200)
				setTimeout(()=>{
					document.querySelector('#accSucc').style.visibility = 'visible'				
				}, 6200)
			}

			if(this.builderIsTriggered == false && document.querySelector('#builderCssOp').offsetTop <= window.scrollY + 400){
				this.builderIsTriggered = true
			}
		})
	}

}
</script>
