<template>
    <div class="fileWrap">
        <div class="sectionTitleWrap">File manager</div>
        <div class="sectionDescription">
            An app that allows easy transfer from smartphone to pc by automaticlly locating pictures and displaying them for easy selection.
        </div>
        <div class="section f">
            <DescBox boxImg="./file/folder.svg" boxTitle="Easy access to smartphone photos" boxText="Easelly select and copy pictures form your phone to your computer"/>
            <div class="fileMain">
                <div class="fileTopWtap">
                    <div>
                        <div class="dot"></div>
                    </div>
                    <div>
                        <img src="../../public/file/w.svg" class="sosd">
                        <img src="../../public/file/ww.svg" class="sosd">
                        <img src="../../public/file/t.svg" class="sosd">
                    </div>
                </div>
                <div v-on:click='select' class="fileMidWrap">
                    <div class="photoWrap">
                        <img src="../../public/file/1.jpg" class="filImg">
                        <img src="../../public/file/2.jpg" class="filImg">
                        <img src="../../public/file/3.jpg" class="filImg">
                    </div>
                    <div class="photoWrap">
                        <img src="../../public/file/4.jpg" class="filImg">
                        <img src="../../public/file/5.jpg" class="filImg">
                        <img src="../../public/file/6.jpg" class="filImg">
                    </div>                    
                </div>
                <div class="brsdfA">
                <div class="fileBottomWrap">
                    <div v-show="selectedCount > 0" class="fileBotWrap">
                        {{selectedCount}} Files selected
                    </div>
                </div>
                    <div v-on:click="copyFiles" v-show="selectedCount > 0" class="fileBotWrap copyBtn">
                        Copy Files
                </div>
                </div>

            </div>
        </div>
    </div>
</template>

<script>
import DescBox from './DescBox.vue'

export default {
    name: 'File',

    components: {
        DescBox
    },

    data(){
        return{
            selectedCount: 0
        }
    },

    methods: {
        select: function(e){
            if(e.target.classList.contains('filImg')){
                if(e.target.classList.contains('selectedPic')){
                    e.target.classList.remove('selectedPic')
                    this.selectedCount --
                } else {
                    e.target.classList.add('selectedPic')
                    this.selectedCount ++ 
                }
            }
        },

        copyFiles: function(){
            document.querySelectorAll('.filImg').forEach(el=>{
                el.classList.remove('selectedPic')
                this.selectedCount = 0
            })
        }
    }
}
</script>

<style>

    .copyBtn{
        cursor: pointer;
    }

    .copyBtn:hover{
        box-shadow: 1px 1px 2px 2px rgb(15, 15, 15);
    }

    .brsdfA{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }

    .copyBtn{
        background: #303030;
        color: white;
    }

    .fileBottomWrap{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        height: 80px;
    }

    .fileWrap{
        padding-top: 40px;
        background: #161714;
        border-bottom: 5px solid #00B1FF;
    }

    .fileMain{
        display: flex;
        flex-direction: column;
        width: 60%;
        margin-top: 50px;
        max-width: 600px;
    }

    .fileTopWtap{
        height: 50px;
        background: #303030;
        border-radius: 2px;
        display: flex;
        justify-content: space-between;
    }

    .fileMidWrap{
        margin-top: 20px;
    }

    .photoWrap{
        display: flex;
        flex-direction: row;
        justify-content: space-around;
    }

    .filImg{
        width: 100%;
        height: auto;
        object-fit: cover;
        margin: 5px;
        box-shadow: 0 0 5px rgb(19, 19, 19);
        cursor: pointer;
        border-radius: 2px;
    }

    .filImg:hover{
        transform: scale(1.02,1.02);
        box-shadow: 0 0 5px 5px rgb(19, 19, 19);
    }

    .dot{
        background: rgb(22, 149, 6);
        border-radius: 50%;
        border: 2px solid white;
        box-shadow: 0 0 5px green;
        height: 6px;
        width: 6px;
        justify-content: space-between;
    }

    .fileTopWtap{
        display: flex;
        padding: 0 20px;
        align-items: center;
    }

    .fileTopWtap img{
        height: 12px;
        width: auto;
        margin: 0 10px;
        cursor: pointer;
    }

    .fileBotWrap{
        background: #303030;
        height: 50px;
        padding: 0 20px;
        display: flex;
        align-items: center;
        width: max-content;
        color: white;
        border-radius: 4px;
    }


    .selectedPic{
        outline: 2px solid white;
    }

            @media all and (max-width: 925px){

            .fileMain{
                width: 90%;
            }
        }
</style>